/* rubik-700 - latin */
@font-face {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/font_files/rubik-v11-latin-700.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../fonts/font_files/rubik-v11-latin-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/font_files/rubik-v11-latin-700.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/font_files/rubik-v11-latin-700.woff') format('woff'), /* Modern Browsers */
       url('../fonts/font_files/rubik-v11-latin-700.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/font_files/rubik-v11-latin-700.svg#Rubik') format('svg'); /* Legacy iOS */
  font-display: swap
}

